<template lang="html">
  <div class="page">
    <cube-scroll ref="scroll" :data="List" :options="opts" @pulling-down="onPullingDown">
      <list :is-empty="!List.length">
        <item :img-src="item.CoverImage"
        v-for="(item,index) in List" :key="index"
        :title="item.Title"
        :desc="item.Summary"
        :href="'/home/detail/' + index">
          <div class="text-footer" slot="text-footer">所属单位：三分厂</div>
          <div class="label" slot="label"> 已完成 </div>
        </item>
      </list>
    </cube-scroll>
  </div>
</template>

<script>
export default {
  data(){
    return {
      List:[],
      obj:{},
      opts:{
        scrollbar:{
          fade: true
        },
        pullDownRefresh: {
          threshold: 90,
          stop: 40,
          txt: '刷新成功'
        }
      }
    }
  },
  created () {
    this.getListData()
  },
  methods : {
    getListData() {
      this.id = this.$route.query.id
      this.axios.get('/static/data.json').then((res) => {
        this.List = res.data.List
        this.obj = res.data.List[this.id]
      })
    },
    onPullingDown() {
      // 模拟更新数据
      setTimeout(() => {
        if (Math.random() > 0.5) {
          this.$createToast({
            time: 1000,
            txt: '正在加载新数据'
          }).show()
        } else {
          // 如果没有新数据
          this.$refs.scroll.forceUpdate()
        }
      }, 1000)
    }

  },
}
</script>

<style lang="stylus">
@import "assets/stylus/base"
</style>



